:root {
  --text-color-1: white;
  --gray-background: rgb(161, 156, 156);
}

html {
  box-sizing: border-box;
  background: plum;
}

a {
  text-decoration: none;
  color: var(--text-color-1);
}

button {
  margin-left: 8vw;
}

h2 {
  background-image: url("./images/emotional-help.jpeg");
  height: 10rem;
  background-size: contain;
  background-repeat: no-repeat;
  margin: 5vh 15vw;
  padding-top: 7vh;
  text-align: center;
  font-size: 10px;
  border-radius: 10px;
}

.fa-bars {
  color: black;
  visibility: visible;
}

.navbar-div {
  display: flex;
  justify-content: space-around;
  background: var(--gray-background);
  height: 5vh;
  width: 100vw;
  margin: 0;
  position: fixed;
  align-items: center;
  visibility: hidden;
}
 
.hamburger-nav-div {
   height: 5vh;
}

@media screen and (min-width: 700px) {
  .fa-bars {
    visibility: hidden;
    background: gray;
  }

  .navbar-div {
    visibility: visible;
  }

  .body-div {
    visibility: hidden;
  }
}
